import React, { useState } from 'react';
import cssVar from 'views/mobileView/styles';
import db from 'libs/db';
import MainHeader from 'views/mobileView/components/molecules/Headers/MainHeader';
import { AiOutlineRight } from 'react-icons/ai';
import TopPannel from './TopPanel';
import { toast } from 'react-toastify';
import AdminServices from 'services/api/AdminServices';
import { getDDMMYYY } from 'views/mobileView/components/atoms/DOBFormatter';
import { baseImgURL } from 'assets/Constant';

export default function MFAccountDetails(props) {
    const [clientData, setClientData] = useState({});
    const [loading, setLoading] = useState();

    React.useEffect(() => {
        setLoading(true)
        try {
            db.get(db.model.user).then(async (user) => {
                await getClientData(user.userData.id)
                setLoading(false)
            })
        } catch (error) {
            toast.error(error);
            setLoading(false)
        }
    }, [])

    const getClientData = async (id) => {
        await db.get(db.model.clientData).then(async (p) => {
            if (p === null) {
                let res = await AdminServices.getClient(id).then(res => res);
                if (res.type === true) {
                    const a = JSON.parse(res.data);
                    let aa = a.reduce((aa, f) => { return { ...aa, ...f } }, {})
                    console.log("Get Client API :", aa)
                    setClientData(aa)
                    await db.set(db.model.clientData, JSON.stringify(aa))
                }
            } else {
                setClientData(p)
            }
        })
    }

    return (
        <div className='mainContainer'>
            <MainHeader header='Profile' navigation={props.history} />
            <div className='dataContainer px-0'>
                <TopPannel />
                <div className='menu-card'>
                    <img src={baseImgURL+"menu_user.webp"} className='menu-card-icon' />
                    <div className='menu-card-body'>
                        <div className='bodyTextDark'>Your PAN is {clientData?.Client?.[0].PAN}</div>
                        <div className='smallTextLight'>
                            Gender: {clientData?.Client?.[0].Gender === 1 ? "Male" : "Female"}<br />
                            Tax status: {clientData?.Client?.[0].TaxStatus}<br />
                            Date of birth: {getDDMMYYY(clientData?.Client?.[0].DateofBirth)}
                        </div>
                    </div>
                </div>
                <div className='menu-card'>
                    <img src={baseImgURL+"menu_user.webp"} className='menu-card-icon' />
                    {
                        clientData?.Nominees?.length > 0 ?
                            <div className='menu-card-body'>
                                <div className='bodyTextDark'>Your nominee is ‘{clientData?.Nominees?.[0].Name}’</div>
                                <div className='smallTextLight'>
                                    Relationship with you: {clientData?.Nominees?.[0].Relation}<br />
                                    Date of birth: {getDDMMYYY(clientData?.Client?.[0].DateofBirth)}
                                </div>
                            </div>
                            :
                            <div className='menu-card-body'>
                                <div className='bodyTextDark'>You have not set a nominee</div>
                            </div>
                    }
                </div>
                <div className='menu-card'>
                    <img src={baseImgURL+"menu_user.webp"} className='menu-card-icon' />
                    <div className='menu-card-body'>
                        <div className='bodyTextDark'>Your bank is ‘{clientData?.BankInfo?.[0].BankName}’</div>
                        <div className='smallTextLight'>
                            Account number: {clientData?.BankInfo?.[0].AccountNumber}<br />
                            IFSC: {clientData?.BankInfo?.[0].IFSC}<br />
                            Bank mandate status: Inactive
                        </div>
                        <div className='header3dark secondaryGreen'>
                            Set up bank mandate <AiOutlineRight size={12} />
                        </div>
                    </div>
                </div>
                <div style={{ padding: "4%" }}>
                    <div className='header1faint'>Want to correct or update details?</div>
                    <div className='smallTextLight'>Drop us an email on support@mintbox.ai</div>
                    <div style={{ marginTop: '4%', border: "1px solid " + cssVar.getPropertyValue('--light'), backgroundColor: cssVar.getPropertyValue('--lightest'), borderRadius: "8px", padding: "4%" }}>
                        <div className='header1faint secondaryGreen'>Important</div>
                        <div className='smallTextLight'>Above details are related to only your mutual fund account with mintbox. You can find your demat  account details on your broker’s platform</div>
                    </div>
                </div>
            </div>
        </div>
    )
}
